import { request } from '../utils/requestToJs';

const prefix = '/api/students';

export const getStudentProfile = ({ studentId } = {}) =>
  request({
    prefix,
    url: `/Portal/GetStudentProfile/${studentId}`,
    method: 'GET',
    params: {},
    data: {},
  });

