import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getClassOwnerActivitys = async({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) => {
  const result = await request({
    prefix,
    url: '/ClassOwnerActivity',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  })
  if(result.data && result.data.items) {
    result.data.items.forEach(item => {
      item.key = `${item.classId}_${item.teacherId}_${item.yearEduId}_${item.semesterId}`
    });
  }
  console.log(result);
  return result;
};

export const saveClassOwnerActivity = data =>
  request({
    prefix,
    url: '/ClassOwnerActivity', 
    method: 'POST',
    params: {},
    data
  });

export const exportClassOwnerActivities = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId
} = {}) =>
  requestDownload({
    prefix,
    url: '/ClassOwnerActivity/ExportClassOwnerActivities',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId
    },
    defaultFileName: 'ClassOwnerActivity.xlsx',
  });



