import React, { Component } from 'react';
import {
  Card,
  Row,
  Col,
  InputNumber,
  Input,
  Form,
  message,
  Table,
  Upload,
  Button,
  Icon,
  Spin,
} from 'antd';
import moment from 'moment';
import { DatePickerString } from 'components/common';
import { BtnAddNew, BtnSave } from 'components/common/button';
import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import {
  saveClassOwnerActivity,
} from 'services/classOwnerActivity';
import {
  getTeacherOwnerClassActivity
} from 'services/studentPortal';
import { hostAPI } from 'utils';
import { objectEquals } from 'utils/functionUtil';
import SearchBar from './SearchBar';
// import { history } from '../../../states/store';
import './style.scss';

@Form.create({ name: 'ClassOwnerActivityForm' })
class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      loading: false,
      paramSearch: {},
      dataKLCVforPopup: null,
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = nextProps => {
    if (!objectEquals(nextProps.params, this.props.params)) {
      this.handleChangeQueryParams();
    }
  };

  setValueToKey = (value, id, key) => {
    console.log('value', value);
    const dataTemp = { ...this.state.dataKLCVforPopup };
    const foundIndex = dataTemp.classOwnerActivities.findIndex(x => x.id === id);
    dataTemp.classOwnerActivities[foundIndex][key] = value;
    this.setState({ dataKLCVforPopup: dataTemp });
  };

  handleChangeQueryParams = async () => {
    const paramSearch = getQueryParams();
    this.setState({
      paramSearch,
    });
    if (
      !paramSearch.classId
      ||
      !paramSearch.yearEduId
      ||
      !paramSearch.semesterId
    ) {
      return;
    }
    this.setState({
      dataKLCVforPopup: [],
    });
    paramSearch.staffId = JSON.parse(window.localStorage.getItem('app.claims')).userId;
    const result = await getTeacherOwnerClassActivity(paramSearch);
    if(result && result[0]){
      const dataKLCVforPopup = {
        classOwnerActivities: result[0].data.items
      }
      this.setState({
        dataKLCVforPopup,
      });
    }
    
  };

  handleChangeActivityDate = (value, id) => {
    this.setValueToKey(value, id, 'activityDate');
  };

  handleChangeNoOfDay = (value, id) => {
    this.setValueToKey(value, id, 'noOfDay');
  };

  handleChangeContent = (value, id) => {
    this.setValueToKey(value.target.value, id, 'activityContent');
  };

  handleUploadFile = ({ file }, id) => {
    this.setValueToKey(file, id, 'newFile');
  };

  handClickAddNewCer = () => {
    const tempData = { ...this.state.dataKLCVforPopup };
    tempData.classOwnerActivities.push({
      id: `NEW-${Math.random()}`,
      activityDate: new Date(),
      noOfDay: 1,
      activityContent: 'Khng c',
      attachment: null,
    });
    this.setState({ dataKLCVforPopup: tempData });
  };

  handleOKPopup = () => {
    console.log('ok popup');
    console.log(this.state.dataKLCVforPopup);
    this.props.form.validateFields(async err => {
      if (!err) {
        const patt = new RegExp('NEW');
        const { dataKLCVforPopup, reload } = this.state;
        try {
          const tempData = { ...dataKLCVforPopup };
          const checkDup = [];
          let isDup = false;
          tempData.classOwnerActivities.forEach(item => {
            const tempDate = moment(item.activityDate).format("YYYY-MM-DD");
            console.log(tempDate)
            if (checkDup[tempDate]) {
              isDup = true
            } else {
              checkDup[tempDate] = true
            }
          })
          if (isDup) {
            message.warn("Ngy sinh hot b trng!");
            return;
          }
          if (dataKLCVforPopup) {
            const { paramSearch } = this.state;
            const formData = new FormData();
            formData.append('classId', paramSearch.classId);
            formData.append('teacherId', paramSearch.staffId);
            formData.append('yearEduId', paramSearch.yearEduId);
            formData.append('semesterId', paramSearch.semesterId);
            const tempActivities = [];
            tempData.classOwnerActivities.forEach(item => {
              const formatItem = {
                id: item.id,
                noOfDay: item.noOfDay,
                activityContent: item.activityContent,
                activityDate: item.activityDate,
                attachmentId: item.attachment,
              };
              if (patt.test(formatItem.id)) {
                delete formatItem.id;
              }
              if (item.newFile) {
                delete formatItem.attachmentId;
                formatItem.tempId = item.newFile.name;
                formData.append('files', item.newFile, item.newFile.name);
              }
              tempActivities.push(formatItem);
            });
            formData.append('activitiesString', JSON.stringify(tempActivities));
            await saveClassOwnerActivity(formData);
            message.success('Cp nht thnh cng');
            this.setState({
              reload: reload + 1,
              dataKLCVforPopup: null,
            }, () => {
              this.handleChangeQueryParams();
            });
          }
        } catch (error) {
          message.error('C li xy ra');
        }
      }
    });
  };

  handleCancelPopup = () => {
    console.log('cancel popup', this.state.dataKLCVforPopup);
    this.setState({
      dataKLCVforPopup: null,
    });
  };

  handleRemoveFile = () => {
    console.log('handleRemoveFile');
  };

  OnSearch = (yearEduId, semesterId) => {
    const { paramSearch } = this.state;
    // history.push(`/sinh-hoat-lop?yearEduId=${yearEduId}&semesterId=${semesterId}&classId=${paramSearch.classId}`);
    window.history.pushState('sinh-hoat-lop', 'SinhHoatLop', `#/sinh-hoat-lop?yearEduId=${yearEduId}&semesterId=${semesterId}&classId=${paramSearch.classId}`);
    this.handleChangeQueryParams();
  }

  render() {
    const { loading } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columnsPopup = [
      {
        title: 'Ngy sinh hot',
        dataIndex: 'activityDate',
        render: (text, record) => (
          <Form.Item>
            {getFieldDecorator(`activityDate${record.id}`, {
              initialValue: record.activityDate,
              rules: [
                {
                  required: true,
                  message: 'Vui lng chn Ngy sinh hot!',
                },
              ],
            })(
              <DatePickerString
                placeholder="Chn ngy"
                onChange={date => this.handleChangeActivityDate(date, record.id)}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Khi lng (ngy)',
        dataIndex: 'noOfDay',
        render: (text, record) => (
          <Form.Item>
            {getFieldDecorator(`noOfDay${record.id}`, {
              initialValue: record.noOfDay,
              rules: [
                {
                  required: true,
                  message: 'Vui lng nhp Khi lng (ngy)!',
                },
              ],
            })(
              <InputNumber
                min="0"
                max="1"
                step="0.1"
                onChange={value => this.handleChangeNoOfDay(value, record.id)}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Ni dung',
        dataIndex: 'activityContent',
        render: (text, record) => (
          <Form.Item>
            {getFieldDecorator(`activityContent${record.id}`, {
              initialValue: record.activityContent,
              rules: [
                {
                  required: true,
                  message: 'Vui lng nhp Ni dung!',
                },
              ],
            })(<Input onChange={value => this.handleChangeContent(value, record.id)} />)}
          </Form.Item>
        ),
      },
      {
        title: 'File',
        dataIndex: 'attachment',
        render: (text, record) => {
          let customProps = {};
          if (record.newFile) {
            customProps = {
              fileList: [
                {
                  uid: record.id,
                  name: record.newFile.name,
                  status: 'done',
                },
              ],
            };
          } else if (record.attachment) {
            customProps = {
              fileList: [
                {
                  uid: record.id,
                  name: record.attachment,
                  status: 'done',
                  url: `${hostAPI}/api/filestorage/Storage/${record.attachment}/Download`,
                },
              ],
            };
          }
          return (
            <div style={{ verticalAlign: "top" }} >
              <Upload
                name="file"
                listType="text"
                onRemove={this.handleRemoveFile}
                beforeUpload={() => false}
                onChange={uploadData => this.handleUploadFile(uploadData, record.id)}
                showUploadList={{
                  showRemoveIcon: false,
                  showDownloadIcon: false,
                }}
                {...customProps}
              >
                <Button>
                  <Icon type="upload" /> Upload
                </Button>
              </Upload>
            </div>
          );
        },
      },
    ];

    return (
      <Card title="Khi lng cng vic c vn / ch nhim" className="class-owner-activity">
        <Row gutter={16}>
          <Col span={24}>
            <SearchBar onSearch={this.OnSearch} />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <BtnAddNew onClick={() => this.handClickAddNewCer()} />
          </Col>
          <Col span={12} style={{ textAlign: 'right' }} >
            <BtnSave onClick={() => this.handleOKPopup()} />
          </Col>
        </Row>
        <Spin spinning={loading}>
          <Table
            rowClassName="class-owner-activity"
            columns={columnsPopup}
            dataSource={
              this.state.dataKLCVforPopup && this.state.dataKLCVforPopup.classOwnerActivities
            }
            bordered
            rowKey="id"
          />
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



